header {
  width: 100%;
}

.headergrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  position: relative;
  height: 70px;
  width: 600px;
}

.headergrids2 {
  float: right;
  right: 0;
}

.meny {
  /* lägger menyn på en rad, ist för som en lista */
  color: whitesmoke;
  display: inline-block;
  top: 0;
  z-index: 1000000000;
}

.menyhere {
  text-decoration: underline;
  color: whitesmoke;
  display: inline-block;
  top: 0;
  z-index: 1000000000;
  text-decoration-style: double;
}

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
  transform: none;
  z-index: 1000000000;
  margin-top: 0.8em;
}

.meny a {
  z-index: 1000000000;
  margin: auto;
  padding: 10px;
  transform: none;
  color: whitesmoke;
  text-decoration: none;
  font-size: 1.2em;
  font-family: "Quicksand", sans-serif;
}

.meny a:hover {
  transition: 0.8s;
  text-decoration: underline;
}

.ham:hover {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .container {
    display: inline-block;
    position: relative;
    width: 100%;
    padding-bottom: 40%;
    overflow: hidden;
    margin-top: -5em;
  }

  header {
    height: 100%;
    min-height: 400px;
    top: 0;
  }

  .headergrid {
    width: 100%;
  }

  .logo {
    max-width: 130px;
    background-color: rgba(253, 216, 251, 0.2);
    border-radius: 30px;
    margin-top: 10px;
    margin-left: 10px;
  }

  .ham {
    float: right;
    margin-right: 5px;
  }

  .navbar {
    top: 0;
    margin-top: 60px;
    background-color: rgba(255, 255, 255, 0.95);
    width: 98%;
    height: 500px;
    overflow: hidden;
    color: whitesmoke;
    transform: translateY(-120%);
    transition: transform 0.4s ease;
  }

  .showNav {
    transform: translateY(0);
  }

  .navbar ul {
    height: 90%;
    list-style: none;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
  }

  .navbar ul li a {
    color: black;
    padding-bottom: 10px;
    text-decoration: none;
    font-family: "Quicksand", sans-serif;
    font-size: 1.4rem;
  }

  .navbar ul li a:hover,
.navbar ul li a:focus {
    color: #151e2d;
    text-decoration: underline;
  }

  .lineone,
.linetwo,
.linethree {
    width: 38px;
    height: 5px;
    border-radius: 25px;
    background-color: whitesmoke;
    margin: 7px;
    transition: 0.5s;
    z-index: 99999999;
    box-shadow: 1px 1px 1px 1px #2e2d2d;
  }

  .change .lineone {
    margin-right: 1em;
    transform: rotate(-45deg) translate(-1px, 20px);
  }

  .change .linetwo {
    opacity: 0;
  }

  .change .linethree {
    transform: rotate(45deg) translate(5px, -19px);
  }
}
